import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  // 主页面
  {
    path: '/',
    name: 'Main',
    component: ()=>import('../views/Main.vue'),
    children:[
      // 首页
      {
        path: '/',
        name: 'Home',
        component: Home
      },
      // 课程
      {
        path: '/class',
        name: 'Class',
        component: () => import('../views/Class.vue')
      },
      // 约课记录
      {
        path: '/classbuy',
        name: 'ClassBuy',
        component: () => import('../views/ClassBuy.vue')
      },
      // 练习
      {
        path: '/prictice',
        name: 'Prictice',
        component: () => import('../views/Prictice.vue')
      },
      // 我的
      {
        path: '/mine',
        name: 'Mine',
        component: () => import('../views/Mine.vue')
      },
      {
        path: '/login',
        name: 'Login',
        component: () => import('../views/login/Login.vue')
      },
      {
        path: '/Forget-pass',
        name: 'Forget-pass',
        component: () => import('../views/login/Forget-pass.vue')
      },
      {
        path: '/Sms-login',
        name: 'Sms-login',
        component: () => import('../views/login/Sms-login.vue')
      },

    ]
  },
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
